//    mobile-

.theme-summer .header {
  &__top {
    @media screen and (max-width: $viewport--md) {
      background: linear-gradient(92.39deg, #693131 0%, #d38c77 100%);
    }
  }
}

//   

.theme-summer .main-nav {
  background: #603813;
  background: -webkit-linear-gradient(to right, #b29f94, #603813);
  background: linear-gradient(to left, #b29f94, #603813);
}

//   mobile-

.theme-summer .main-nav {
  @media screen and (max-width: $viewport--md) {
    background: #8d6052;
  }
}

//  
.theme-summer .link {
  background-color: #f05a5a;
}

//     instagram

.theme-summer .social-slide {
  background-color: #994f4f;
}
